import { Box, Button, Container, Stack, Typography } from '@mui/material'
import { isDisabled } from '@testing-library/user-event/dist/utils';
import React, {useState} from 'react'


export default function App() {
 let [counter,setCounter] = useState(0);
 

 const handleAdd = ()=>{
  setCounter(++counter)
 }

 const handleSubtract = ()=>{
  setCounter(--counter)
 }

 const handleRestart = ()=>{
  setCounter(0)
 }

 

  return (
    <>
      <Container 
        sx={{
          display: "flex",
          alignItems: 'center',
          justifyContent: 'center',
          height:"720px",


      }}>
        <Box 
          sx={{
            backgroundColor: "rgba(73, 165, 219, 0.559)",
            width: "50%",
            padding:"20px",
            display: "flex",
            alignItems: 'center',
            justifyContent: 'center',
            flexDirection:"column",
            boxShadow: "rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px",
        }}>



       <Typography variant="h1" gutterBottom>
         {counter}
      </Typography>

      <Stack direction="row" spacing={2}>
       <Button variant="contained" onClick={handleAdd}>+</Button>
       <Button variant="contained" onClick={handleSubtract} disabled={counter=='0'}>-</Button>
       <Button variant="outlined" onClick={handleRestart}>Restart</Button>
        
      </Stack>
      
       

        </Box>
      </Container>
    </>
  );
}

